import { Box } from '@chakra-ui/react';
import styled from '@emotion/styled';
import axios from 'axios';
import { useMemo, useState } from 'react';
import { useNavigate } from 'react-router-dom';

import {
  type ProductDetailRequestParams,
  useGetProductDetail,
} from '@/api/hooks/useGetProductDetail';
import { useGetProductOptions } from '@/api/hooks/useGetProductOptions';
import { BASE_URL } from '@/api/instance';
import { Button } from '@/components/common/Button';
import { useAuth } from '@/provider/Auth';
import { getDynamicPath, RouterPath } from '@/routes/path';
import type { ProductOptionsData } from '@/types';
import { orderHistorySessionStorage } from '@/utils/storage';

import { CountOptionItem } from './OptionItem/CountOptionItem';

type Props = ProductDetailRequestParams;

export const OptionSection = ({ productId }: Props) => {
  const { data: detail } = useGetProductDetail({ productId });
  const { data: options } = useGetProductOptions({ productId });

  const [selectedOption, setSelectedOption] = useState<ProductOptionsData | null>(null);
  const [countAsString, setCountAsString] = useState('1');
  const [isRegistration, setIsRegistration] = useState(false);

  const totalPrice = useMemo(() => {
    return detail.price * Number(countAsString);
  }, [detail, countAsString]);

  const navigate = useNavigate();
  const authInfo = useAuth();

  //
  const handleClick = () => {
    if (!authInfo) {
      const isConfirm = window.confirm(
        '  .\n  ?',
      );

      if (!isConfirm) return;
      return navigate(getDynamicPath.login());
    } else if (!selectedOption) {
      alert('  .');

      return;
    }

    orderHistorySessionStorage.set({
      optionName: selectedOption.name,
      optionId: selectedOption.id,
      productName: detail.name,
      imageUrl: detail.imageUrl,
      productId: parseInt(productId),
      quantity: parseInt(countAsString),
    });

    navigate(RouterPath.order);
  };

  //  
  const handleRegistration = async () => {
    const token = localStorage.getItem('token');

    if (!selectedOption) {
      alert('  ');
      return;
    }

    axios
      .post(
        `${BASE_URL}/api/wishes`,
        {
          optionId: selectedOption?.id,
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
            'Content-Type': 'application/json',
          },
        },
      )
      .then((response) => {
        console.log(response.data);
        setIsRegistration(true);
        alert('  ');
      })
      .catch((error) => {
        console.error('  :', error);
        alert('  .');
      });
  };

  return (
    <Wrapper>
      <TopWrapper>
        <CountOptionItem
          selectedOption={selectedOption}
          setSelectedOption={setSelectedOption}
          productName={detail.name}
          options={options}
          count={countAsString}
          onChange={setCountAsString}
        />
        <Box mt="15px"> </Box>
        <Button theme="outline" size="large" onClick={handleRegistration} disabled={isRegistration}>
          {isRegistration ? ' ' : ' '}
        </Button>
      </TopWrapper>
      <BottomWrapper>
        <PricingWrapper>
             <span>{totalPrice}</span>
        </PricingWrapper>
        <Button theme="black" size="large" onClick={handleClick}>
           
        </Button>
      </BottomWrapper>
    </Wrapper>
  );
};

const Wrapper = styled.div`
  width: 100%;
  padding: 30px 12px 30px 30px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
`;

const BottomWrapper = styled.div`
  padding: 12px 0 0;
`;

const TopWrapper = styled.div`
  padding: 12px 0 0;
`;

const PricingWrapper = styled.div`
  margin-bottom: 20px;
  padding: 18px 20px;
  border-radius: 4px;
  background-color: #f5f5f5;
  display: flex;
  justify-content: space-between;

  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  color: #111;

  & span {
    font-size: 20px;
    letter-spacing: -0.02em;
  }
`;
